.side-nav {
  display: block;
  padding-bottom: 40px;
  color: #808080;
  background-color: rgba(255, 255, 255, 0.8);
  padding-left: 4px;
  border-radius: 3px;
  width: 200px;
  height: 500px;
  position: fixed;
  top: 120px;
  right: 30px;
}
.side-nav.fold {
  background-color: rgba(255, 255, 255, 0);
}
.side-nav.fold .nav-toggle {
  background-color: rgba(255, 255, 255, 0.8);
}
.side-nav.fold .nav-toggle::before {
  content: "+";
}
.side-nav.fold .nav-body {
  display: none;
}
.side-nav a {
  color: #828282;
}
.side-nav a:hover {
  color: #0085a1;
}
.side-nav .nav-toggle-wrapper {
  margin-top: 10px;
  margin-bottom: 0;
  margin-left: 17px;
  cursor: pointer;
}
.side-nav .nav-toggle-wrapper .nav-toggle {
  padding-left: 10px;
  border-radius: 3px;
  font-size: 20px;
}
.side-nav .nav-toggle-wrapper .nav-toggle::before {
  content: "âˆ’";
  position: relative;
  margin-right: 5px;
  bottom: 1px;
}
.side-nav .nav-body {
  position: relative;
  list-style: none;
  height: 460px;
  overflow-x: hidden;
  overflow-y: auto;
  padding-left: 0px;
  padding-right: 5px;
  text-indent: 0;
}
.side-nav .nav-body li {
  position: relative;
  list-style: none;
}
.side-nav .nav-body li a {
  padding-left: 10px;
  max-width: 180px;
  display: inline-block;
  vertical-align: middle;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  text-decoration: none;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.side-nav .nav-body li.h1_nav {
  margin-left: 0;
  font-size: 15px;
}
.side-nav .nav-body li.h2_nav {
  margin-left: 5px;
  font-size: 14px;
}
.side-nav .nav-body li.h3_nav {
  margin-left: 10px;
  font-size: 13px;
}
.side-nav .nav-body li.h1_nav,
.side-nav .nav-body li.h2_nav,
.side-nav .nav-body li.h3_nav {
  font-weight: bold;
}
.side-nav .nav-body li.h4_nav {
  margin-left: 18px;
  font-size: 12px;
}
.side-nav .nav-body li.h5_nav {
  margin-left: 25px;
  font-size: 10px;
}
.side-nav .nav-body li.h6_nav {
  margin-left: 40px;
  font-size: 9px;
}
.side-nav .nav-body li.h4_nav a,
.side-nav .nav-body li.h5_nav a,
.side-nav .nav-body li.h6_nav a {
  max-width: 170px;
}
.side-nav .nav-body li.active {
  border-radius: 4px;
  background-color: #F5F5F5;
}
.side-nav .nav-body li.active a {
  color: #0085a1!important;
}
#backtotop {
  position: fixed;
  bottom: 15%;
  right: 2em;
  z-index: 104;
}
#backtotop .backtotop-btn {
  position: relative;

  width: 38px;
  height: 38px;
  border-radius: 3px;

  background-color: #E4E4E4;
  cursor: pointer;
}
#backtotop .backtotop-btn:hover {  background-color: #e2e2e2;  }
#backtotop .arrow-upward, #backtotop .vertical-stick {
  position: absolute;
  right: 0;
  left: 0;
  margin: auto;
}
#backtotop .arrow-upward {
  top: -1px;

  width: 0;
  height: 0;
  
  border: 9px solid transparent;
  border-bottom-color: #404040;
}
#backtotop .backtotop-btn:hover .arrow-upward {  border-bottom-color: #0085a1;  }
#backtotop .vertical-stick {
  top: 15px;

  width: 8px;
  height: 14px;
  
  border-radius: 1px;
  background-color: #404040;
}
#backtotop .backtotop-btn:hover .vertical-stick {  background-color: #0085a1;  }

.hiding {
  display: none;
}
